import React from 'react';



interface BarberProfile {
  _id: string;
  employeeName: string;
  employeeFees: number;
  specialCut: string;
  description?: string;
  employeeImage?: string;
  pendingAppointments: Appointment[];
  completedAppointments: Appointment[];
}

const ProfileView: React.FC = () => {
  
    const dispatch: AppDispatch = useDispatch();
    const { barbers, loading, error } = useSelector((state: RootState) => state.barber);
  
    useEffect(() => {
      dispatch(fetchBarbers());
    }, [dispatch]);

  const barber: BarberProfile = {
    employeeName: barbers.empoloyeeName,
    specialCut: "Master Special Specialist",
    description: "A barber is a grooming expert specializing in haircuts, beard trims, and styling. They produce quality appearances with precision and care.",
    employeeImage: "/placeholder-profile.jpg", 
    pendingAppointments: [
      { date: "4/10/2025", time: "11:00 Pm" },
      { date: "4/10/2025", time: "1:00 Pm" },
      { date: "4/10/2025", time: "3:00 Pm" },
      { date: "4/10/2025", time: "6:00 Pm" }
    ],
    completedAppointments: [
      { date: "4/9/2025", time: "12:00 Pm" },
      { date: "4/9/2025", time: "3:00 Pm" },
      { date: "4/9/2025", time: "5:00 Pm" },
      { date: "4/9/2025", time: "6:00 Pm" }
    ]
  };

  return (
    <div className="bg-black text-white p-4">
      <div className="max-w-4xl mx-auto border border-blue-500 rounded p-4">
        <button className="bg-black text-white border border-gray-600 px-4 py-1 rounded mb-4">
          Go Back
        </button>
        
        <h2 className="text-center text-xl mb-6">Profile View</h2>
        
        {/* Profile Card */}
        <div className="bg-gray-900 rounded-lg p-6 mb-6">
          <div className="flex gap-6">
            {/* Profile Image */}
            <div className="w-24 h-24 rounded-full overflow-hidden flex-shrink-0">
              <img 
                src="/api/placeholder/100/100" 
                alt="Profile" 
                className="w-full h-full object-cover"
              />
            </div>
            
            {/* Profile Details */}
            <div className="flex-1">
              <div className="mb-6">
                <span className="font-medium">Description:</span> {barber.description}
              </div>
              
              <div className="mb-6">
                <p className="text-sm mb-1">NAME: {barber.name}</p>
                <p className="text-sm text-gray-400">{barber.title}</p>
              </div>
              
              <div className="flex justify-end gap-3">
                <button className="bg-gray-700 text-white px-4 py-1 rounded-full text-sm">
                  Proceed
                </button>
                <button className="bg-gray-700 text-white px-4 py-1 rounded-full text-sm">
                  Delete
                </button>
                <button className="bg-gray-700 text-white px-4 py-1 rounded-full text-sm">
                  Exit
                </button>
              </div>
            </div>
          </div>
        </div>
        
        {/* Appointments Section */}
        <div className="flex gap-6">
          {/* Pending Works */}
          <div className="flex-1 bg-gray-900 rounded-lg p-4">
            <h3 className="text-center mb-4">Pending Works</h3>
            
            {barber.pendingAppointments.map((appointment, index) => (
              <div 
                key={`pending-${index}`} 
                className="bg-gray-800 p-3 rounded mb-2 text-sm"
              >
                DateTime: {appointment.date} - {appointment.time}
              </div>
            ))}
          </div>
          
          {/* Completed Works */}
          <div className="flex-1 bg-gray-900 rounded-lg p-4">
            <h3 className="text-center mb-4">Completed Work</h3>
            
            {barber.completedAppointments.map((appointment, index) => (
              <div 
                key={`completed-${index}`} 
                className="bg-gray-800 p-3 rounded mb-2 text-sm"
              >
                DateTime: {appointment.date} - {appointment.time}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileView;